.container{
    width: 90%;
    height: fit-content;
    border: solid black;
    margin-left: 5%;
}
.userContainer{
    display: grid;
    grid-template-columns: 0.2fr 1fr;
    width:100%;
    margin-top: 10px;
   
    border: solid black;

}
.userImg{
    height: 30px;
    width: 30px;
    border: solid black;
}

.userImg img{
       height: 30px;
    width: 30px; 
}
.userName{
    width: 100px;
    border: solid black;
    border: solid black;
}
.postContainer{
    background-color: rgb(208, 207, 207);
    width: 100%;
    height: fit-content;
    border: solid gray 0.5px;
    border-radius: 20px;
}