.container{
  margin-left: 10%;
  display: grid;
  width: 100%;
  height: 100vh;
  border: solid;
  border-radius: 30px;
  border-color: rgb(103, 103, 103);
  border-width: 0.5px;
  background-color: rgb(73, 1, 1);
}
.subcontainer{ 
  border: solid;
display: grid;
grid-template-rows: 0.8fr 0.3fr 1fr;
}


