.Container{
 
    display: grid;
    grid-template-rows: 0.5fr 0.5fr;
    width: 100%;
    height:fit-content;
    border:  rgb(103, 103, 103) solid;
    border-radius: 30px;
    border-width: 0.5px;
    background-color: rgb(70, 70, 70);
}

button{
    cursor: pointer;
    background-color: rgb(70, 70, 70);
    border: solid 0.5px  rgb(103, 103, 103);
    border-radius: 10px;
    height: 50px;
    width: 150px;
    color: white;
    font-size: 20px;
}
button:hover{
    border-width: 2px;
    border-bottom: solid  rgb(70, 70, 70);
    color: rgba(0, 255, 34);
    transition: 400ms;
}

@media (max-width:768px){
    .Container{margin-top: 120px;}
}


@media (max-width:690px) {
    .Container{margin-top: 120px;}
}

