

.container {
  margin-top: 2%;
  margin-left: 5%;
  display: grid;
  grid-template-columns: 1fr 1fr;
background-image: url("logo.jpg");
border-radius: 20px;
background-size: 100%  120%;
width: 90%;
opacity: 90%;
height: 100%;
border: solid black;

}


.ava{
padding-top: 2%;
padding-left: 5%;
height: 80%;
width: 15%;
}

.ava img{
  background-color: rgba(146, 242, 239, 0.17);
  border: solid;
  border-color: rgba(146, 242, 239, 0.359);
  height: 80%;
  width: 100%;
  border-radius: 50%;
}

.right h1{
  font-size: 40px;
  color: rgba(3, 105, 76, 0.919);
  text-decoration: underline  rgba(3, 105, 76, 0.919);
}

textarea{
  border-radius: 20px;
  font-size: 20px;
  height: 80px;
  width: 100%;
  margin-top: 0px;
}
input[type=file]{
  display: none;
}

label{
  margin-left: 40%;
  display: block;
  width: 30px;
  height: 30px;
  cursor: pointer;
}
label img:hover{
box-shadow: 0px 0px 1px 1px white;
transition: 500ms;
}
.right{
  display: flex;
  justify-content: center;
  align-items: center;
}

@media (max-width:1280px) {
  .right h1{font-size: 35px;}
}
@media (max-width:1024px) {
  .right h1{font-size: 30px;}
}
@media (max-width:768px) {
  .right h1{font-size: 20px;}
}

@media (max-width:690px) {
  .right h1{font-size: 25px;}
}

@media (max-width:500px) {
  .right h1{font-size: 20px;}
}

@media (max-width:410px) {
  .right h1{font-size: 15px;}
}


@media (max-width:366px) {
  .right h1{font-size: 12px;}
}

@media (max-width:344px) {
  .right h1{font-size: 10px;}
}
